import { Component, OnInit, TemplateRef, ViewChild, AfterViewInit, OnDestroy } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { DataTableDirective } from 'angular-datatables';
import { ToastrService } from 'ngx-toastr';
import { CategoryManagementService } from '../../shared/services/category-management.service';
import { Subject } from 'rxjs';
import { CommonService } from '../../shared/services/common.service';

@Component({
  selector: 'app-category-management',
  templateUrl: './category-management.component.html'
})
export class CategoryManagementComponent implements AfterViewInit, OnDestroy, OnInit {

  @ViewChild('deleteModal') public deleteModal: ModalDirective;
  @ViewChild(DataTableDirective)
  dtElement: DataTableDirective;
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject();
  categoryList: Array<any>;
  categoryId: any;
  datatbaleData: any;

  constructor(
    private titleService: Title,
    private toastr: ToastrService,
    private categoryManagementService: CategoryManagementService,
    public commonService: CommonService
  ) { }

  ngOnInit(): void {
    this.titleService.setTitle("Category Management");
    this.getCategoryList();
  }

  getCategoryList() {
    this.dtOptions = {
      //pagingType: 'full_numbers',
      pageLength: 100,
      //order: [1, 'desc'],
      serverSide: true,
      processing: true,
      ajax: (dataTablesParameters: any, callback) => {
        dataTablesParameters.page = parseInt(dataTablesParameters.start) / parseInt(dataTablesParameters.length) + 1;
        dataTablesParameters.limit = dataTablesParameters.length;
        dataTablesParameters.search_param = dataTablesParameters.search.value;

        // sorting
        dataTablesParameters.order[0].column == 0 ? dataTablesParameters.name = (dataTablesParameters.order[0].column == 0 && dataTablesParameters.order[0].dir == "asc") ? ("name") : ("-name") : "";
        dataTablesParameters.order[0].column == 1 ? dataTablesParameters.description = (dataTablesParameters.order[0].column == 1 && dataTablesParameters.order[0].dir == "asc") ? ("description") : ("-description") : "";
        this.datatbaleData = dataTablesParameters;

        this.categoryManagementService
          .categorylist(dataTablesParameters)
          .subscribe(
            respones => {
              let resData = JSON.parse(JSON.stringify(respones));
              if (resData.status_code == 200) {
                this.categoryList = resData.data;

                callback({
                  recordsTotal: resData.total_record,
                  recordsFiltered: dataTablesParameters.search_param == "" ? resData.total_record : resData.filter_record,
                  data: []
                });
              } else {
                this.toastr.error('', resData.message);
              }

            },
            err => {
              this.toastr.error('', err.error.message);
            }
          );
      },
      columns: [
        { data: 'name' },
        { data: 'description' },
        { data: 'action', searchable: false, orderable: false }]
    };
  }

  ngAfterViewInit(): void {
    this.dtTrigger.next();
  }

  ngOnDestroy(): void {
    this.dtTrigger.unsubscribe();
  }

  saveId(categoryId) {
    this.categoryId = categoryId;
  }

  deleteCategory() {
    this.categoryManagementService
      .deleteCategory(this.categoryId)
      .subscribe(
        response => {
          let resData = JSON.parse(JSON.stringify(response));
          if (resData.status_code == 200) {
            this.deleteModal.hide();
            this.toastr.success('', "Category deleted successfully.")
            this.rerender();
          } else {
            this.toastr.error('', resData.message);
          }
        },
        err => {
          this.toastr.error('', err.error.mesaage);
        }
      );
  }

  rerender(): void {
    this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
      dtInstance.destroy();
      this.dtTrigger.next();
    });
  }

  exportTable(type) {

    this.datatbaleData.page = "1";
    this.datatbaleData.limit = "100000";
    this.categoryManagementService
      .categorylist(this.datatbaleData).subscribe(response => {
        let resData = JSON.parse(JSON.stringify(response));

        if (resData.status_code == 200) {
          const onlyNameAndSymbolArr = resData.data.map(x => ({
            'Name': x.name || "-",
            'Description': x.description || "-"
          }));

          if (type == 'pdf') {
            if (onlyNameAndSymbolArr.length > 0) {
              var keys = [Object.keys(onlyNameAndSymbolArr[0])]
              var values = onlyNameAndSymbolArr.map(element => Object.values(element));
              this.commonService.exportArrayPDF(keys, values, 'categoryList')
            } else {
              this.toastr.error('', "No Data Found !");
              $("#myDropdown").toggle();
            }
          } else {
            this.commonService.exportCsv(resData.data, onlyNameAndSymbolArr, 'categoryList');
          }

        } else {
          this.toastr.error('', resData.message);
        }
      },
        err => {
          this.toastr.error('', err.error.message);
        });
  }

}
