import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { CategoryManagementService } from '../../../shared/services/category-management.service';
import { Validations } from '../../../shared/validators/Validations';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-add-category',
  templateUrl: './add-category.component.html'
})
export class AddCategoryComponent implements OnInit {
  
  addForm: FormGroup;
  submitted: boolean = false;

  constructor(
  	private titleService: Title,
    private categoryManagementService: CategoryManagementService,
    private toastr: ToastrService,
    private router:Router, 
    private route:ActivatedRoute
  ) { }

  ngOnInit(): void {
  	this.titleService.setTitle("Add Category");

    this.addForm = new FormGroup({
      name: new FormControl('', [
        Validators.required,
        Validators.pattern(Validations.whitespace),
        Validators.minLength(2)
      ]),
      description: new FormControl('', [
        Validators.required
      ])
    });
  }

  submitAddForm(){
    this.submitted = true;

      var formData = new FormData();
      formData.append("name", this.addForm.controls.name.value);
      formData.append("description", this.addForm.controls.description.value);

      if (this.addForm.valid) {
      this.categoryManagementService.createCategory(formData).subscribe(response => {
        let resData = JSON.parse(JSON.stringify(response));

        if(resData.status_code == 200){
          this.router.navigate(['category-management']).then(() => {
            this.toastr.success('', "Category added successfully!");
          });
        }else{
          this.toastr.error('', resData.message);
        }
      },
        err => {
          this.toastr.error('', err.error.message);
        });

    }
  }


}
