import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';
import { CategoryManagementService } from '../../../shared/services/category-management.service';
import { Validations } from '../../../shared/validators/Validations';

@Component({
  selector: 'app-edit-category',
  templateUrl: './edit-category.component.html'
})
export class EditCategoryComponent implements OnInit {
  
  editForm: FormGroup;
  submitted: boolean = false;
  categoryId : any;

  constructor(
  	private titleService: Title,
    private categoryManagementService: CategoryManagementService,
    private toastr: ToastrService,
    private router:Router, 
    private route:ActivatedRoute
  ) { }

  ngOnInit(): void {
  	this.titleService.setTitle("Edit Category");

    this.editForm = new FormGroup({
      name: new FormControl('', [
        Validators.required,
        Validators.pattern(Validations.whitespace),
        Validators.minLength(2)
      ]),
      description: new FormControl('', [
        Validators.required
      ])
    });

    this.getCategoryInfo();
  }

  getCategoryInfo(){
  	this.route.queryParams.subscribe(param => {
      this.categoryId = param.id;
    });
    
    this.categoryManagementService.getCategory(this.categoryId).subscribe(response => {
        let resData = JSON.parse(JSON.stringify(response));

        if(resData.status_code == 200){
          this.editForm.patchValue({
          	name: resData.data.name,
          	description: resData.data.description
          });
        }else{
          this.toastr.error('', resData.message);
        }
      },
        err => {
          this.toastr.error('', err.error.message);
        });
  }

  submitEditForm(){
    this.submitted = true;

      var formData = new FormData();
      formData.append("name", this.editForm.controls.name.value);
      formData.append("description", this.editForm.controls.description.value);

      if (this.editForm.valid) {
      this.categoryManagementService.updateCategory(this.categoryId,formData).subscribe(response => {
        let resData = JSON.parse(JSON.stringify(response));

        if(resData.status_code == 200){
          this.router.navigate(['category-management']).then(() => {
            this.toastr.success('', "Category updated successfully!");
          });
        }else{
          this.toastr.error('', resData.message);
        }
      },
        err => {
          this.toastr.error('', err.error.message);
        });

    }
  }

}
