import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../environments/environment';

@Injectable()
export class CategoryManagementService {

  constructor(private http: HttpClient) { }

  categorylist(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/categorieslist/`, data);
  }

  getCategory(id) : Observable<any>{
    return this.http.get<any>(`${environment.base_url}/create_category/${id}/view/`);
  }

  createCategory(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/create_category/`, data);
  }

  updateCategory(id,data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/create_category/${id}/edit/`, data);
  }

  deleteCategory(id): Observable<any> {
    return this.http.delete<any>(`${environment.base_url}/create_category/${id}/`);
  }
  
}
